.settings{
    display: flex;
    align-items: center;
    width: 200px;
    height: 300px;
    position: absolute;
    top: 50%;
    right: -202px;
    border-radius: 7px;
    transition: .7s;
    background-color: #444444;
    transition-timing-function: cubic-bezier(.18,1.01,.83,1.01);

    &.activeMenu{
        right: 0;
    }

    .exit-menu-button{
        position: absolute;
        top: 0;
        right: 0;
        width: 25px;
        height: 25px;
        border-bottom-left-radius: 7px;
        background-color: #ffd700;
        font-size: 23px;

        &:hover{
            background: white; 
        }
    }

    .settings-area{
        width: 120px;
        height: 220px;
        border: 1px solid #ffd700;
        border-radius: 7px;
        display: flex; 
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: relative;

        .settings-button{
            width: 75px;
            height: 35px;
            color: white;
            font-size: 18px;
            border-radius: 10px;
            background: #ffd700;
            color: black;

            &:hover{
               background: white; 
            }
        }
    }

    .buttons-container{
        width: 50px;
        height: 261px;
        display: flex;
        flex-direction: column;
        position: relative;
        left: -50px;

        .settings-button{
            width: 50px;
            height: 50px;
            margin-bottom: 2px;
            background-color: #3a3a3a;
            color: white;
            font-size: 25px;
            padding-right: 2px;
            display: flex;
            justify-content: flex-start;
            padding-left: 12px;

            &:hover{
                background-color: #4f4f4f;
            }

            &.active{
                border-right: 2px solid #ffd700;
            }
        }
    }       
}

.modal-export-menu{
    position: absolute;
    top: 36%;
    right: 44%;

    .export-menu{
        width: 350px;
        height: 270px;
        border-radius: 5px;
        background-color: #444444;
        box-shadow: 0 0 4px #ffd700;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        color: white;
        font-size: 20px;
        padding: 10px; 
    
        .export-field{
            height: 40px;
            width: 280px;
            display: flex;
            justify-content: space-around;
            align-items: center;
        }
    
        .export-exit-button{
            position: absolute;
            top: 1px;
            right: 1px;
            width: 25px;
            height: 25px;
            font-size: 23px;
            border-radius: 5px;
            background-color: #ffd700;
        }
    
        .export-button{
            width: 80px;
            height: 30px;
            font-size: 23px;
            border-radius: 5px;
            background-color: #ffd700;
        }
    
        .nameInput{
            background-color:  #444444;
            color: white;
            font-size: 20px;
            border: 1px solid white;
            width: 150px;
            height: 30px;
            padding: 0 10px;
            outline: none;
        }
    
        .color-button{ 
            width: 35px;
            height: 35px;
            border-radius: 100%;
        
            .color-input{
                 display: none;
            }
        }  
    }
}