.main-canvas-container{
    align-self: center;
    width: 768px;
    height: 768px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 5px white;
    background: linear-gradient( #d8d8d8, transparent .60px), linear-gradient( 90deg, #d8d8d8, transparent .60px);
    background-size: 24px 24px;
    background-position: center center;
    overflow: hidden;
}

.frame-info{
    width: 95px;
    height: 50px;
    position: absolute;
    top: 5px;
    right: 50%;
    color: #ffd700;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}