.layer-settings{
    width: 320px;
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    border: 3px solid gray;
    border-radius: 7px;

    .layer-settings-header{
        height: 35px;
        width: 320px;
        border-radius: 1px;
        display: flex;
        justify-content: center;
        align-items: center;

        .add-layer-button{
            width: 150px;
            height: 35px;
            font-size: 20px;
            font-weight: 600;
            background-color: #ffd700;
            border-radius: 5px;

            &:hover{
                background-color: white; 
            }

            svg{
                font-size: 24px;
                margin-right: 8px;
            }
        }
    }

    .layer-settings-container{
        height: 170px;
        width: 320px;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow: auto;
        

        .layer-view{
            width: 280px;
            height: 35px;
            margin: 5px 0;
            background: #3a3a3a;
            display: flex;
            justify-content: space-around;
            align-items: center;
            color: white;
            font-size: 20px;
            border-radius: 5px;

            &.active{
                border: 1px solid #ffd700;
            }

            .layers-button-container{
                width: 110px;
                height: 35px;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .layer-button{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 25px;
                    height: 25px;
                    color: black;
                    font-size: 16px;
                    background-color: #ffd700;
                    border-radius: 5px;
                    padding-bottom: 2px;

                    &:hover{
                    background-color: white; 
                    }

                    &.position{
                        font-size: 28px;
                    }
                }
            } 
        }
    }
}