@font-face {
    font-family: 'PixelFont';
    font-style: italic;
    font-weight: 700;
    src: url(assets/fonts/16799.ttf) format('TrueType');
}

*{
    margin: 0;
    padding: 0;
    border: none;
}

a, a:visited, a:active {
    color: inherit;
    text-decoration: none;
}

button{
    cursor: pointer;
    outline: none;
}